<template>
  <div class="container px-4 mx-auto">
    
    <div class="box-shadow from-transparent to-ark-800 relative pointer-events-none select-none">
      <img src="https://pbs.twimg.com/profile_banners/1356337672604819456/1612472100/1500x500" class="w-full mx-auto pointer-events-none select-none" alt="">
      <div class="text-gray-50 bg-opacity-10 md:absolute bottom-2 right-0 p-2 mt-4 bg-gray-400 rounded shadow" v-if="showCountdown">
        <div class="mx-2 mb-1 text-xs font-semibold tracking-wide text-center text-white uppercase rounded">{{ isStart ? 'Servidor en linea' : 'Apertura dentro de' }}</div>
        <div class="flex text-center">
          <div class="w-14 flex flex-col flex-1 mx-2">
              <p class="text-gray-50 text-xl font-semibold">{{ days }}</p>
              <p class="text-xs font-medium text-gray-400">Dias</p>
          </div>
          <div class="w-14 flex flex-col flex-1 mx-2">
              <p class="text-gray-50 text-xl font-semibold">{{ hours }}</p>
              <p class="text-xs font-medium text-gray-400">Horas</p>
          </div>
          <div class="w-14 flex flex-col flex-1 mx-2">
              <p class="text-gray-50 text-xl font-semibold">{{ minutes }}</p>
              <p class="text-xs font-medium text-gray-400">Minutos</p>
          </div>
          <div class="w-14 flex flex-col flex-1 mx-2">
              <p class="text-gray-50 text-xl font-semibold">{{ seconds }}</p>
              <p class="text-xs font-medium text-gray-400">Segundos</p>
          </div>
        </div>
      </div>
    </div>

    <router-view/>

  </div>
</template>

<script>
import moment from 'moment-timezone';

export default {
  mounted() {
    window.setInterval(() => {
      this.timeNow = Math.trunc(moment.tz("Europe/Madrid").valueOf() / 1000);
      this.timeStart = Math.trunc(moment.tz(this.dateStart, "Europe/Madrid").valueOf() / 1000);
    },1000);
  },
  data() {
    return {
      dateStart: '2021-02-09 21:00:00',
      timeNow: Math.trunc(moment.tz("Europe/Madrid").valueOf() / 1000),
      timeStart: Math.trunc(moment.tz(this.dateStart, "Europe/Madrid").valueOf() / 1000),
      isStart: false,
      showCountdown: false,
    }
  },
  computed: {
    seconds() {
      if (!this.isStart) this.isStart = (this.timeNow > this.timeStart ? true : false);
      return (this.timeNow > this.timeStart ? (this.timeNow - this.timeStart) : (this.timeStart - this.timeNow)) % 60;
    },
    minutes() {
      return Math.trunc((this.timeNow > this.timeStart ? (this.timeNow - this.timeStart) : (this.timeStart - this.timeNow)) / 60) % 60;
    },
    hours() {
      return Math.trunc((this.timeNow > this.timeStart ? (this.timeNow - this.timeStart) : (this.timeStart - this.timeNow)) / 60 / 60) % 24;
    },
    days() {
      return Math.trunc((this.timeNow > this.timeStart ? (this.timeNow - this.timeStart) : (this.timeStart - this.timeNow)) / 60 / 60 / 24);
    }
  }
}
</script>

