<template>
  <div class="home">
    <!-- <input type="text" size="20" class="bg-ark-600 text-gray-50 w-full px-3 py-2 mt-4 text-sm text-center rounded outline-none" placeholder="Buscar ..." v-model="inputSearchUser" v-bind:readonly="!listUsers"> -->
    <section class="p-4">
      <div class="flex flex-wrap -mx-8 text-center" v-if="filterListUsers.length > 0">
        
        <div
          class="md:w-1/3 w-full px-4 mb-8"
          v-for="(itemUser, indexUser) in filterListUsers" :key="indexUser"
        >
          <div class="bg-ark-600 flex flex-col h-full py-4 space-y-4 text-gray-100 rounded shadow">
            <img
              class="rounded-50 w-24 h-24 mx-auto border-4 pointer-events-none select-none"
              v-bind:class="getPlataformColor('border', getPlataform(itemUser.stream).toLowerCase())"
              :src="itemUser.picture"
              alt=""
            >
            
            <div class="flex flex-col flex-1 px-6 space-y-3">
              <div>
                <h3 class="text-xl font-semibold">{{ itemUser.name }}</h3>
                <span class="text-gray-400">{{ !!getPlataform(itemUser.stream) ? `Directos en ${getPlataform(itemUser.stream)}` : `Sin información` }}</span>
              </div>
              <div class="separator-row from-gray-300 opacity-10 to-transparent w-full"></div>
              <div class="flex flex-wrap items-center justify-center flex-1">
                <img class="opacity-60 hover:opacity-90 w-5 h-6 mx-2 my-1 cursor-pointer" v-if="!!itemUser.twitter" @click="goToURL(itemUser.twitter)" src="../assets/img/twitter.svg" alt="">
                <img class="opacity-60 hover:opacity-90 w-5 h-6 mx-2 my-1 cursor-pointer" v-if="!!itemUser.instagram" @click="goToURL(itemUser.instagram)" src="../assets/img/instagram.svg" alt="">
                <img class="opacity-60 hover:opacity-90 w-5 h-6 mx-2 my-1 cursor-pointer" v-if="!!itemUser.facebook" @click="goToURL(itemUser.facebook)" src="../assets/img/facebook.svg" alt="">
                <img class="opacity-60 hover:opacity-90 w-6 h-6 mx-2 my-1 cursor-pointer" v-if="!!itemUser.youtube" @click="goToURL(itemUser.youtube)" src="../assets/img/youtube.svg" alt="">
                <img class="opacity-60 hover:opacity-90 w-5 h-6 mx-2 my-1 cursor-pointer" v-if="!!itemUser.twitch" @click="goToURL(itemUser.twitch)" src="../assets/img/twitch.svg" alt="">
              </div>
              <div class="separator-row from-gray-300 opacity-10 to-transparent w-full"></div>
            </div>

            <div class="px-6 space-y-3">
              <button
                class="focus:outline-none bg-ark-700 inline-block w-full px-8 py-3 text-sm font-semibold rounded-md shadow"
                v-bind:class="getPlataformColor('hover', getPlataform(itemUser.stream).toLowerCase())"
                @click="goToURL(itemUser.stream)"
              >VER DIRECTO</button>
            </div>
          </div>
          

        </div>

      </div>
      <div class="flex flex-wrap -mx-8 text-center" v-else>
        <div class="bg-ark-600 w-full py-10 mx-4 text-gray-300 rounded shadow">No se encontraron registros.</div>
      </div>
    </section>
  </div>
</template>

<script>

import participants from '@/users.json';
import _ from 'lodash';

export default {
  name: 'Home',
  components: {},
  data() {
    return {
      listUsers: participants,
      inputSearchUser: '',
      orderRandom: false,

    }
  },
  computed: {
    filterListUsers() {
      let listParticipants = _.filter((this.orderRandom ? _.shuffle(participants) : participants), ['status', 1]);
      return listParticipants.filter(item => {
        // console.log(item.name.toLowerCase().includes(this.inputSearchUser.toLowerCase().trim()));
        return item.name.toLowerCase().includes(this.inputSearchUser.toLowerCase().trim());
      });
    },
  },
  methods: {
    getPlataform(url) {
      let plataform = '';
      if (!url) return plataform;
      const plataforms = ['Twitch', 'Youtube', 'Facebook', 'Trovo', 'Mixer'];
      _.map(plataforms, item => {
        if (url.includes(item.toLowerCase())) {
          plataform = item;
        }
      });
      
      return plataform;
    },
    getPlataformColor(type, plataform) {
      if (!plataform) return (type === 'hover' ? 'hover:bg-gray-600' : 'border-gray-50') 
      const plataformColor =  {
        border: {
          'twitch': 'border-purple-500 opacity-90',
          'youtube': 'border-red-500',
          'facebook': 'border-blue-500',
          'trovo': 'border-green-500'
        },
        hover: {
          'twitch': 'hover:bg-purple-700',
          'youtube': 'hover:bg-red-600',
          'facebook': 'hover:bg-blue-700',
          'trovo': 'hover:bg-green-700'
        }
      }

      return plataformColor[type][plataform]
    },
    goToURL(url) {
      if (!!url) window.open(url, '_blank');
    }
  },

}
</script>

